header{
    height: 100vh;
    padding-top: 10vh;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ===========  Socials =========== */

.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__social::after{
    content: '';
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

/* ===========  IMG =========== 
 or
    position: absolute;
    width: 22rem;
    height: 30rem;
    left: 50%
    transform: transformX(-50%)
*/

.me{
    position: absolute;
    width: 30rem;
    height: 30rem;
    left: calc(50% - 15rem);
    margin-top: 2rem;
}

/* ===========  SCROLL DOWN =========== */

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
.scroll__down:hover{
    color: var(--color-green);
}

/* ===========  MEDIA QUERIES (medium devices) =========== */

@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
    .me{
        position: absolute;
        width: 22rem;
        height: 30rem;
        left: calc(50% - 11rem);
        margin-top: 2rem;
    }
}

/* ===========  MEDIA QUERIES (small devices) =========== */
@media screen and (max-width: 600px){
    header {
        height: 100vh;
    }

    .header__social,
    .scroll__down {
       display: none;
    }
}